import styled from "styled-components"

export const StyledNewsSourceList = styled.section `
    margin-top: -50px;
    background-color:#212121;
   > div{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    place-items:center;
   }
    h4 {
        margin-top:-26px;
        color:white !important;
    }
    `